.Footer {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(52, 116, 201, 0.9)),
    to(rgba(53, 231, 249, 0.9))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: -o-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: linear-gradient(
    90deg,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  /* transform: translateY(-18px); */
  margin-top: -20px;
  height: 15vh;
}

.Footer ul {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.Footer ul li {
  margin: 0 0.5vw;
  color: #fff;
}

.Footer ul li a {
  color: #fff;
  opacity: 1;
}
.Footer ul li a:hover {
  opacity: 1;
}

@media only screen and (max-width: 768px) {
  .Footer {
    margin-top: -15px;
  }
  .Footer ul li {
    margin: 0 1vw;
  }
}
@media only screen and (max-width: 768px) {
  .Footer {
    margin-top: -12px;
  }
  .Footer ul li {
    margin: 0 1.5vw;
  }
}



