.About {
  height: 100%;
  color: #706f6f;
  margin-bottom: 5vh;
}

.About h3 {
  text-align: center;
  font-size: 1.8em;
  margin-bottom: 1.2vh;
}

.About hr {
  margin-bottom: 3vh;
  width: 3vw;
  border: 0.5px solid #706f6f;
}

.skills {
  display: -ms-flexbox;
  display: flex;
  width: 70vw;
  margin: 0 auto;
}

.skill {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.skill:nth-child(1) {
  -webkit-animation: left 2.5s ease-in-out;
          animation: left 2.5s ease-in-out;
}

@-webkit-keyframes left {
  0% {
    -webkit-transform: rotate(0) translateX(-500%);
            transform: rotate(0) translateX(-500%);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
}

@keyframes left {
  0% {
    -webkit-transform: rotate(0) translateX(-500%);
            transform: rotate(0) translateX(-500%);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
}

.skill:nth-child(2) {
  -webkit-animation: right 2.5s ease-in-out;
          animation: right 2.5s ease-in-out;
}

@-webkit-keyframes right {
  0% {
    -webkit-transform: rotate(0) translateX(500%);
            transform: rotate(0) translateX(500%);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
}

@keyframes right {
  0% {
    -webkit-transform: rotate(0) translateX(500%);
            transform: rotate(0) translateX(500%);
    opacity: 1;
  }
  50% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) translateX(0);
            transform: rotate(0) translateX(0);
    opacity: 1;
  }
}

.skill img {
  margin: 2vh 5vw;
  max-height: 200px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.skill h4 {
  margin: 0;
}

.skill h5 {
  font-weight: 400;
}

@media only screen and (max-width: 768px) {
  .skills {
    width: 90vw;
  }
  .skill img {
    max-height: 180px;
  }
  .skill h5 {
    width: 95%;
  }
  .About hr {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .skills {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



