.Graph {
  height: 40vh;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(52, 116, 201, 0.9)),
    to(rgba(53, 231, 249, 0.9))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: -o-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: linear-gradient(
    90deg,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  padding: 5vh 2vw;
}



