.Projects {
  /* min-height: 100vh; */
  height: 100%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Projects h2 {
  font-size: 2em;
  margin-bottom: 5vh;
  font-weight: 400;
  /* color: #706f6f; */
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(103, 0, 255, 0.5)),
    to(rgba(0, 97, 255, 0.77))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background-image: linear-gradient(
    to right,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
.MyProjects {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Project {
  min-height: 40vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-bottom: 1px solid #eeeefe;
  margin: 2vh 0;
  padding-bottom: 5vh;
}

.Project img {
  width: 80%;
  height: 106%;
}

.img {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
}

.ProjectText {
  width: 47%;
  color: #706f6f;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.ProjectText h3 {
  font-weight: 400;
  text-align: left;
  margin: 0;
}

.Buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 50%;
  margin: 5vh auto 0;
}

.Buttons h5 {
  line-height: 3.5;
  width: 100%;
  margin: 0;
}

.Buttons a {
  width: 40%;
  cursor: pointer;
  margin: 0 0.5vw;
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-decoration: none;
}

.Demo {
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(rgba(103, 0, 255, 0.5)),
    to(rgba(0, 97, 255, 0.77))
  );
  background: -webkit-linear-gradient(
    right,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background: -o-linear-gradient(
    right,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background: linear-gradient(
    -90deg,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  color: #eeeefe;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-weight: 400;
}

.Demo:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 100%;
  background: rgba(103, 0, 255, 0.1);
  -webkit-transition: all 2s ease;
  -o-transition: all 2s ease;
  transition: all 2s ease;
  color: #fff;
}
.Demo:hover:before {
  width: 100%;
}

.Code {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(52, 116, 201, 0.9)),
    to(rgba(53, 231, 249, 0.9))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: -o-linear-gradient(
    left,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  background: linear-gradient(
    90deg,
    rgba(52, 116, 201, 0.9),
    rgba(53, 231, 249, 0.9)
  );
  color: #fff;
  font-weight: 400;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Code:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 100%;
  background: rgba(53, 231, 249, 0.2);
  -webkit-transition: all 2s ease;
  -o-transition: all 2s ease;
  transition: all 2s ease;
  color: #fff;
}
.Code:hover:before {
  width: 100%;
  color: #fff;
}

.technology {
  display: -ms-flexbox;
  display: flex;
  width: 95%;
}

.technology ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0;
}

.technology ul li {
  padding: 0.7vh 2vw;
  margin: 1vh 1vw;
  border-radius: 5px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(103, 0, 255, 0.5)), to(rgba(0, 97, 255, 0.77)));
  background: -webkit-linear-gradient(rgba(103, 0, 255, 0.5), rgba(0, 97, 255, 0.77));
  background: -o-linear-gradient(rgba(103, 0, 255, 0.5), rgba(0, 97, 255, 0.77));
  background: linear-gradient(rgba(103, 0, 255, 0.5), rgba(0, 97, 255, 0.77));
  color: #eeeefe;
}

@media only screen and (max-width: 992px) {
  .Projects {
    min-height: 100%;
  }
  .img {
    width: 60%;
  }
  .ProjectText {
    width: 40%;
  }
  .ProjectText h3,
  .ProjectText h2 {
    width: 95%;
    text-align: center;
    margin: 0 auto;
  }

  .ProjectText h2 {
    margin-bottom: 2vh;
  }

  .Buttons {
    width: 70%;
    margin-top: 3vh;
  }
}

@media only screen and (max-width: 768px) {
  .Project {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 70%;
  }
  .ProjectText {
    width: 100%;
    margin-top: 5vh;
  }
  .img {
    width: 100%;
  }
  .Buttons {
    width: 80%;
  }
}

@media only screen and (max-width: 600px) {
  .Project {
    min-height: 60%;
  }
}



