.Home {
  height: 60vh;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(103, 0, 255, 0.5)),
    to(rgba(0, 97, 255, 0.77))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background: -o-linear-gradient(
    left,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  background: linear-gradient(
    90deg,
    rgba(103, 0, 255, 0.5),
    rgba(0, 97, 255, 0.77)
  );
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /* flex-direction: column; */
  text-align: center;
}

.Home h1 {
  margin-top: 0;
  color: #fff;
  font-size: 3.5em;
  font-weight: 400;
  width: 80vw;
  -webkit-animation: title 2.5s ease;
          animation: title 2.5s ease;
}

@-webkit-keyframes title {
  0% {
    -webkit-transform: rotate(0) translateY(-500%);
            transform: rotate(0) translateY(-500%);
  }
  30% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(00);
            transform: rotate(0) scale(1) translateX(0) translateY(00);
    opacity: 1;
  }
  58% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(00);
            transform: rotate(0) scale(1) translateX(0) translateY(00);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(0);
            transform: rotate(0) scale(1) translateX(0) translateY(0);
    opacity: 1;
  }
}

@keyframes title {
  0% {
    -webkit-transform: rotate(0) translateY(-500%);
            transform: rotate(0) translateY(-500%);
  }
  30% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(00);
            transform: rotate(0) scale(1) translateX(0) translateY(00);
    opacity: 1;
  }
  58% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(00);
            transform: rotate(0) scale(1) translateX(0) translateY(00);
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(0) scale(1) translateX(0) translateY(0);
            transform: rotate(0) scale(1) translateX(0) translateY(0);
    opacity: 1;
  }
}

.HomeIcons {
  width: 10vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.HomeIcons ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  list-style: none;
  -webkit-animation: icons 2.5s ease-in-out;
          animation: icons 2.5s ease-in-out;
}
@-webkit-keyframes icons {
  0% {
    -webkit-transform: translateX(500%);
            transform: translateX(500%);
  }
  50% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes icons {
  0% {
    -webkit-transform: translateX(500%);
            transform: translateX(500%);
  }
  50% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.HomeIcons ul li {
  margin: 1.2vh 0.7em;
  color: #fff;
}

.HomeIcons ul a {
  color: #fff;
}

@media only screen and (max-width: 992px) {
  .Home h1 {
    font-size: 2.8em;
  }
}

@media only screen and (max-width: 768px) {
  .Home {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .HomeIcons ul {
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 0;
    margin: 2vh auto;
  }
}

@media only screen and (max-width: 600px) {
  .Home h1 {
    font-size: 1.5em;
  }
  .fa-4x {
    font-size: 3em;
  }
}



